/* ==========================================================================
   #Forum Single (Topic) - Start
   ========================================================================== */
body.forum {
    #bbpress-forums {
        .subscription-toggle {
            position: absolute;
            right: 0;
            padding-top: 63px;
            font-size: 12px;
            font-weight: 700;
            text-transform: uppercase;
            color: $first-main-color;
        }

        .bbp-topics {
            margin: 0;
        }

        .bbp-topic-form {
            border-left: 1px solid $default-border-color;
            border-right: 1px solid $default-border-color;
            border-bottom: 1px solid $default-border-color;
            div.bbp-template-notice {
                padding: 0 30px;
                background-color: transparent;
                border: none;

                p {
                    font-size: 14px;
                    padding: 18px 18px;
                    margin: 0 0 10px 0;
                    color: #303030;
                    background-color: #fff8dd;
                    border: 1px solid #ffe275;
                    display: inline-block;
                    width: 100%;
                    border-radius: 0;
                }
            }
        }

        li.bbp-header {
            ul li {
                &.bbp-topic-title {
                    padding-left: 30px;
                }
            }
        }
        /* Table body */
        li.bbp-body {
            ul.topic {
                border-top: none;
                padding: 29px 30px 26px;

                border-bottom: 1px solid $default-border-color;

                &:last-child {
                    border-bottom: none;
                }

                li.bbp-forum-freshness,
                li.bbp-topic-freshness {
                    color: $default-text-color;

                    > a{
                        color: $default-text-color;
                        font-size: 14px;
                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }
                .bbp-topic-title {
                    padding-right: 20px;
                    .bbp-topic-permalink {
                        font-size: 14px;
                        color: #212121;
                        font-weight: 600;

                        &:hover{
                            color: $first-main-color;
                        }
                    }
                    .bbp-topic-meta {
                        font-size: 14px;
                        .avatar {
                            display: none;
                            vertical-align: middle;
                            margin: 0 5px 0 10px;
                        }

                    }
                }

                .bbp-topic-freshness-author {
                    .bbp-author-avatar {
                        display: none;
                    }
                    .bbp-author-name {
                        font-size: 14px;
                        color: $first-main-color;
                    }
                }
                &.sticky {
                    background-color: transparent !important;
                    font-size: 1em;
                    position: relative;
                    &:after {
                        content: '\f02e';
                        font-family: 'FontAwesome';;
                        color: $first-main-color;
                        font-size: 16px;
                        position: absolute;
                        top: -6px;
                        right: 20px;
                        vertical-align: top;
                    }
                }
            }

            .bbp-forum-info {
                .bbp-forum-content {
                    font-size: 13px;
                    font-weight: 400;
                    color: $default-text-color;
                }
            }

            .bbp-topic-started-by {
                color: $default-text-color;
                font-size: 14px;
                font-weight: 400;
                .bbp-author-name {
                    font-size: 14px;
                    color: $first-main-color;
                    &:hover {
                        color: $default-heading-color;
                    }
                }
            }
        }
        .bbp-topic-form,
        .bbp-no-topic,
        .bbp-reply-form,
        .bbp-no-reply {
            padding: 0 0 46px;
            background-color: #fff;
        }

        @include phone-portrait{
            .subscription-toggle{
                position: relative;
                padding-top: 0;
            }
        }

    }
    .bbp-pagination:nth-of-type(4) {
        display: none;
    }
}
/* ==========================================================================
   #Forum Single (Topic) - End
   ========================================================================== */