/* Template notice style start */
#bbpress-forums {
    div.bbp-template-notice {
        padding: 9px 25px;
        margin: 0 0 10px 0;
        color: $default-text-color;
        background-color: #fefff0;
        border-color: #f1ebaa;
        display: inline-block;
        width: 100%;
        border-radius: 0;
        font-size: 14px;

        p {
            font-size: 14px;
        }

        a {
            color: inherit;

            &:hover{
                color: $first-main-color;
            }
        }

        img.avatar {
            border: none;
            vertical-align: middle;
            margin-bottom: 0;
            margin-left: 5px;
            margin-right: 5px;
            border-radius: 50%;
        }
    }

    .bbp-breadcrumb + div.bbp-template-notice,
    .bbp-topic-tags + div.bbp-template-notice {
        margin: 0 0 20px;
        width: 100%;
    }
}
/* Template notice style end */