/* breadcrumbs styles start */
#bbpress-forums {
    div.bbp-breadcrumb {
        padding: 0 0 10px;
        font-size: 14px;
        float: none;
        display: none;

        p {
            margin: 0;
            > * {
                &:not(:last-child):after {
                    content: "";
                }
            }
        }

        .bbp-breadcrumb-home {
            color: $default-text-color;

            &:hover {
                color: $first-main-color;
            }

        }

        .bbp-breadcrumb-current {
            color: $first-main-color;
        }
    }
}

.edgt-title-holder .edgt-title-wrapper .edgt-breadcrumbs {
    div.bbp-breadcrumb,
    div.bbp-topic-tags {
        font-size: 15px;
    }

    @include phone-portrait{
        div.bbp-breadcrumb{
            display: none;
        }
    }
}
/* breadcrumbs styles end */