/* Pagination style start */
#bbpress-forums {
    .bbp-pagination {
        margin: 17px 0;
        color: $default-text-color;
        font-size: 13px;

        .bbp-pagination-count{
            font-size: 14px;
            color: #ababab;
        }

    }
    .bbp-template-notice + .bbp-pagination {
        margin: 0 0 41px;
    }

    @include phone-portrait{
        .bbp-template-notice + .bbp-pagination {
            margin: 0 0 20px;
        }
    }
}
/* Pagination style end */